body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.slide-container {
  width: 70%;
  margin: auto; }

h3 {
  text-align: center; }

.each-slide > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-size: cover;
  height: 300px;
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}

.each-fade {
  display: -webkit-flex;
  display: flex;
}

.each-fade .image-container {
  width: 75%;
  overflow: hidden;
}

.each-fade .image-container img {
  width: 100%;
}

.each-fade h2 {
  width: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0;
  background: #adceed;
}

.nav, .indicators {display: none !important;}

.react-slideshow-zoom-wrapper {
  width: 90% !important;
  overflow: hidden !important;
  margin-left: auto !important;
  margin-right: auto !important;
  margin-top: 3% !important;
}

.react-slideshow-zoom-wrapper .zoom-wrapper * {
  height: 950px !important;  
}

.dropdown-item:focus, .dropdown-item:hover {
  background-color:inherit !important;
}
.dropzone p {
    outline: 2px dashed #4d4d4d;
    outline-offset: -10px;
    transition: outline-offset .15s ease-in-out, background-color .15s linear;
    height:300px;
    width:100%;  
    background-color: #FFF;  
    color:#4d4d4d;
}

.dropzone p img {
    width: 250px;
}

.filePreview img {
    max-height:300px;
    width:100% !important;
}

input {
    margin-bottom:1.5em !important;
}

.uploadForm { height: 600px; }

.dropFileLabel { 
    font-size: 24px;
    display: block;
    padding-top: 5em;
}
.navbar {     
    /* margin-bottom:10px; */
    background-color: #4D4D4D; 
    height:50px;  
    margin-bottom: 1em;
}

#slideshowNavbar:-webkit-full-screen {
    display:none;
}

#slideshowNavbar:-moz-full-screen {
    display:none;
}

#slideshowNavbar:fullscreen {
    display:none;
}

:-webkit-full-screen .navbar {
    display: none;
  }
  
  :-moz-full-screen .navbar {
    display: none;
  }
  
  :fullscreen .navbar {
    display: none;
  }

@media(min-width:768px) {
    .navbar-nav {
        position: relative;
        top: 10px; 
    }
}

@media(max-width:768px) {
    .navbar-nav {
        background-color: #4D4D4D;
    }
}
.navbar a { color: #FFF; }
.navbar-brand img { height: 70px; }
.navbar-brand {
    text-align: left;
    display: block;    
}

.navbar-collapse {
    z-index: 9999;
    position: relative;
    top: -10px;
}

@media(max-width:1200px) {
    .navbar {
        padding:0 !important;
    }

    .navbar-brand img {
        height: 50px !important;
    }

    .bgawor {
        padding: .4em !important;
       
    }
}

.dropdown-menu {
    background-color: #4D4D4D !important;
}

.dropdown-item {
    padding: 0 2em 0 2em !important;
}
.modal-dialog {    
    top: 45% !important;
    -webkit-transform: translateY(-50%)  !important;
            transform: translateY(-50%)  !important;
    border:none;
    max-width:550px !important;
}

.modal-body {
    padding: .5em !important;
}

.modal-footer {
    padding:0 !important;
    border-top: none !important;
    text-align: center;
    display: block !important;
}

.signup-modal button {
    background-color: #e4e4e4 !important;
    color: #4d4d4d !important;
    display: block;    
    margin-left: auto;
    margin-right: auto;
    border:none;
    height:42px;
    width:120px;
    font-size: 20px !important;
    padding:0;
}

.signup-modal button:hover {
    background-color: #1ba1e1 !important;
    color:#FFF !important;
}

.modal-content {
    background-color:#676767 !important;
    color: #FFF !important;
    padding: 1em;
    border-radius:15px !important;
    padding-left:2em;
    padding-right:2em;
}

.modal-header {
    border-bottom: none !important;    
}
.modal-header div {
    font-size:20px !important;
}

.hidden { visibility: hidden; }

#badEmailAlert {
    padding:3px;
    font-weight:bold;
    text-align: center;
    position: relative;
    top: 10px;    
}

.signup-modal .form-group {
    margin-bottom: 0;
}

.modal-title {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 20px !important;
}

.signup-modal img {
    display: block;
    width: 25%;    
    margin-left: auto;
    margin-right: auto;
    margin-bottom:2.5em;
}

.signup-modal input {
    padding: 5px;
    display:block;
    margin-left:auto;
    margin-right:auto;
    margin-bottom: 1em;
    width: 100%;
}

.email-label {
    text-align: center;
    font-size: 20px !important;
    margin-top: 1em;
    margin-bottom: .5em;    
}

.loginLink {
    cursor: pointer;
    text-decoration: underline;
    display:block;
    text-align: center;
}

.error-label {
    position: relative;
    top: -10px;
    font-style: italic;
}

.closeModal {
    position: fixed;
    right: 15px;
    top: 5px;
    font-size: 20px !important;
    cursor: pointer;
}
.slideshow-container {
  /* background-color: #000;   */
  height:1030px; 
}

.slideshow-container-fullscreen {
  height:1080px;
  background-color: #000;  
}
.btn { margin: 15px; cursor:pointer !important; width:100px; text-align: center;}

@media (min-width:1201px) {
.emailDesign { position: relative; top:-7px; left: -12px;}
}

@media(max-width:1200px) {
    .emailDesign {
        margin:0;
    }
}

.emailDesign {
    background-color:#1BA1E1 !important;
    border-color: #1BA1E1 !important;
}

.danger {
    background-color:red !important;
    border-color: red !important;
}

.addProduct {
    margin-bottom:10px;
    float:right;
}

.dropdown button {
    margin:0px;
}
table { 
    width: 80% !important; 
    margin-left:auto;
    margin-right:auto;
    margin-top:3em;
}
